import 'bootstrap'
import axios from 'axios'
import router from '../router'
import "@/assets/style.css"
export default {
  data () {
    return { 
      name: '',
      email: '',
      password:'',
      age:'',
      emptyFields: false
    }
      
  },
  methods: {
    doRegister() {  
    let headers = {
      "Content-Type": "application/json",
      "Accept": "application/json",
    };
    
  axios({
          method: "POST",
          url:"https://api-nodejs-todolist.herokuapp.com/user/register",
          headers: headers,
          data: {
              "name":this.name,
              "email": this.email,
              "password": this.password,
              "age": this.age
          }
      })
      .then(function (res) {
          console.log(res.data.token)
          router.push({ path: '/'});   
      })
        this.name = '';
        this.email = '';
        this.password = '';
        this.age='';
      }
    }
}
