import 'bootstrap'
import "bootstrap/dist/css/bootstrap.min.css"; 
import axios from 'axios'
import router from '../router'
import "@/assets/style.css"
export default {
  data () {
    return { 
      newTodo: {
        description: "",
        completed: false
      },
        todos: [],
        visibilityList: [
            { name: "全部", value: "all" },
            { name: "進行中", value: "active" },
            { name: "已完成", value: "completed" }
        ],
        visibility: 'all'
    }
      
  },
  created() {
        this.getList()
    },
  methods: {
    getList() {
            let headers = {
                'Authorization': 'Bearer '+sessionStorage.getItem('token' ),
                "Content-Type": "application/json",
                "Accept": "application/json",
                
            };
            axios({
                    method: "GET",
                    url:"https://api-nodejs-todolist.herokuapp.com/task",
                    headers: headers
                })
                .then( res => {
                    console.log(res)
                    if (res.status == 200) {
                        this.todos = res.data.data
                    }
                })
    },
    addTodo(){
            let headers = {
                'Authorization': 'Bearer '+sessionStorage.getItem('token' ),
                "Content-Type": "application/json",
                "Accept": "application/json",
                
            };
            if (!this.newTodo.description) {
                return
            }
            axios({
                    method: "POST",
                    url:"https://api-nodejs-todolist.herokuapp.com/task",
                    headers: headers,
                    data: {
                        "description":this.newTodo.description,
                    }
                })
                .then( res => {
                    console.log(res)
                    this.newTodo.description = ''
                    this.todos.unshift(res.data.data)
                })
        },
        changeComplated(id){
            let headers = {
                'Authorization': 'Bearer '+sessionStorage.getItem('token' ),
                "Content-Type": "application/json",
                "Accept": "application/json",
                
            };
            axios({
                    method: "PUT",
                    url:"https://api-nodejs-todolist.herokuapp.com/task/"+id,
                    headers: headers
                })
                .then( res => {
                    console.log(res)
                })
        },
        removeTodo(todo){
            let headers = {
                'Authorization': 'Bearer '+sessionStorage.getItem('token' ),
                "Content-Type": "application/json",
                "Accept": "application/json",
                
            };
            axios({
                    method: "DELETE",
                    url:"https://api-nodejs-todolist.herokuapp.com/task/"+todo._id,
                    headers: headers
                })
                .then( res => {
                    if (res.status == 200) {
                       console.log( res.data.data)
                       this.todos = this.todos.filter(item => item != todo)
                    }
                })
        },
        getIndex(id) {
            return this.todos.findIndex((el) => el.id == id)
        },
        logout() {
           router.push({ path: '/'}); 
           sessionStorage.clear();
        },
    },computed: {
        filteredTodos() {
            let nowTab = this.visibility
            if(nowTab == 'all'){
                return this.todos
            }else if(nowTab == 'active'){
                return this.todos.filter((todo) => !todo.completed)
            }else if(nowTab == 'completed'){
                return this.todos.filter((todo) => todo.completed)
            }
            return [];

        },
        activeTodosLength() {
                return this.todos.filter((todo) => !todo.completed).length
        }
    }
}
